@use "global/var" as v;

.Header-Calc__Display--Theme {
  .Theme__Option {
    background-color: v.$Dark2;
    padding: 5px 1px;
    border-radius: 10px;
  }
}

.Theme__Option input {
  width: 20px;
  height: 20px;
  appearance: none; /* Oculta el radio predeterminado */
  border: 2px solid #ccc;
  border-radius: 50%; /* Para hacerlos redondos */
  background-color: white;
  cursor: pointer;
  margin-right: 10px;
}

/* Color cuando está seleccionado */
.Theme__Option input:checked {
  background-color: v.$Red; /* Azul */
  border-color: v.$DarkRed;
}

/* Color cuando no está seleccionado */
.Theme__Option input:not(:checked) {
  background-color: v.$Dark2;
  border-color: v.$Dark2;
}
