html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "League Spartan", sans-serif;
  line-height: 1.5;
  /* Estas dos lineas deben cambiar con evento de js */
  background-color: hsl(222, 26%, 31%);
  color: hsl(0, 0%, 100%);
}
body.Theme2 {
  background-color: hsl(0, 0%, 90%);
  color: hsl(222, 26%, 31%);
}
body.Theme2 .Theme__Option {
  background-color: hsl(0, 5%, 81%);
}
body.Theme2 .Theme__Option input:not(:checked) {
  background-color: hsl(0, 5%, 81%);
  border-color: hsl(0, 5%, 81%);
}
body.Theme2 .keyboard-Calc,
body.Theme2 .Screen-Calc {
  background-color: hsl(0, 5%, 81%);
}
body.Theme2 .keyboard-Calc__Blue {
  background-color: hsl(185, 42%, 37%);
  color: hsl(0, 0%, 100%);
  display: block;
  box-shadow: 0 4px 1px hsl(185, 58%, 25%);
}
body.Theme2 .keyboard-Calc__Blue:hover {
  background-color: hsl(185, 42%, 33.3%);
}
body.Theme2 .keyboard-Calc__Red {
  background-color: hsl(25, 98%, 40%);
  color: hsl(0, 0%, 100%);
  display: block;
  width: 70%;
  box-shadow: 0 4px 1px hsl(25, 99%, 27%);
}
body.Theme2 .keyboard-Calc__Red:hover {
  background-color: hsl(25, 98%, 36%);
}
body.Theme3 {
  background-color: hsl(268, 47%, 21%);
  color: hsl(52, 100%, 62%);
}
body.Theme3 .keyboard-Calc__Grid div p {
  box-shadow: 0 2px 0 hsl(285, 91%, 52%);
  background-color: hsl(268, 47%, 21%);
}
body.Theme3 p {
  color: hsl(52, 100%, 62%);
}
body.Theme3 .Theme__Option {
  background-color: hsl(281, 89%, 26%);
}
body.Theme3 .Theme__Option input:not(:checked) {
  background-color: hsl(281, 89%, 26%);
  border-color: hsl(281, 89%, 26%);
}
body.Theme3 .keyboard-Calc,
body.Theme3 .Screen-Calc {
  background-color: hsl(281, 89%, 26%);
}
body.Theme3 .keyboard-Calc__Blue {
  background-color: hsl(290, 70%, 36%);
  color: hsl(0, 0%, 100%);
  display: block;
  box-shadow: 0 2px 0 hsl(285, 91%, 52%);
}
body.Theme3 .keyboard-Calc__Blue:hover {
  background-color: hsl(290, 70%, 32.4%);
}
body.Theme3 .keyboard-Calc__Red {
  background-color: hsl(176, 100%, 44%);
  color: hsl(222, 26%, 31%);
  display: block;
  width: 70%;
  box-shadow: 0 2px 0 hsl(177, 92%, 70%);
}
body.Theme3 .keyboard-Calc__Red:hover {
  background-color: hsl(176, 100%, 39.6%);
}

p {
  font-size: 32px;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

/* Layout */
.Calc {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1440px) {
  .Calc {
    width: 50%;
  }
}

/* Header Position */
.Header-Calc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.Header-Calc__Display {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2rem;
}

.Header-Calc__Display--Theme {
  display: flex;
  flex-direction: column;
}
.Header-Calc__Display--Theme .Theme__Number {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.Header-Calc__Display--Theme .Theme__Option {
  display: flex;
  flex-direction: row;
  align-items: center;
}

/* Keyboard Position  */
.keyboard-Calc {
  user-select: none; /* Evita la selección del texto */
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.keyboard-Calc .keyboard-Calc__Grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
}
@media (min-width: 1440px) {
  .keyboard-Calc .keyboard-Calc__Grid {
    justify-items: left;
    padding-left: 16px;
  }
}
.keyboard-Calc .keyboard-Calc__Grid div p {
  margin: 1rem;
}
.keyboard-Calc .keyboard-Calc__flex {
  display: flex;
  gap: 2rem;
}
.keyboard-Calc p {
  color: hsl(222, 26%, 31%);
  font-weight: bold;
  padding: 10px;
  background-color: hsl(30, 25%, 89%);
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 4px 1px hsl(28, 16%, 65%);
}
.keyboard-Calc p:hover {
  cursor: pointer;
  background-color: hsl(0, 0%, 90%);
}
@media (min-width: 640px) {
  .keyboard-Calc p {
    padding: 10px 40px;
  }
}
.keyboard-Calc .keyboard-Calc__Blue {
  background-color: hsl(225, 21%, 49%);
  color: hsl(0, 0%, 100%);
  display: block;
  box-shadow: 0 4px 1px hsl(224, 28%, 35%);
}
.keyboard-Calc .keyboard-Calc__Blue:hover {
  background-color: hsl(225, 21%, 44.1%);
}
.keyboard-Calc .keyboard-Calc__Blue--70 {
  width: 70%;
}
.keyboard-Calc .keyboard-Calc__Red {
  background-color: hsl(6, 63%, 50%);
  color: hsl(0, 0%, 100%);
  display: block;
  width: 70%;
  box-shadow: 0 4px 1px hsl(6, 70%, 34%);
}
.keyboard-Calc .keyboard-Calc__Red:hover {
  background-color: hsl(6, 63%, 45%);
}

.Screen-Calc {
  overflow-x: auto;
  white-space: nowrap;
  text-align: right;
  margin-right: 1rem;
  font-weight: bold;
}
.Screen-Calc p {
  font-size: 40px;
  padding-right: 53px;
}

/* Border-Radius */
.keyboard-Calc,
.Screen-Calc {
  width: 100%;
  background-color: hsl(223, 31%, 20%);
  padding: 0.5rem;
  border-radius: 10px;
}

.Header-Calc__Display--Theme .Theme__Option {
  background-color: hsl(223, 31%, 20%);
  padding: 5px 1px;
  border-radius: 10px;
}

.Theme__Option input {
  width: 20px;
  height: 20px;
  appearance: none; /* Oculta el radio predeterminado */
  border: 2px solid #ccc;
  border-radius: 50%; /* Para hacerlos redondos */
  background-color: white;
  cursor: pointer;
  margin-right: 10px;
}

/* Color cuando está seleccionado */
.Theme__Option input:checked {
  background-color: hsl(6, 63%, 50%); /* Azul */
  border-color: hsl(6, 70%, 34%);
}

/* Color cuando no está seleccionado */
.Theme__Option input:not(:checked) {
  background-color: hsl(223, 31%, 20%);
  border-color: hsl(223, 31%, 20%);
}