@use "global/var" as v;
@use "sass:color";

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: v.$MainFont;
  line-height: 1.5;

  /* Estas dos lineas deben cambiar con evento de js */
  background-color: v.$Dark1;
  color: v.$White;

  &.Theme2 {
    background-color: v.$Lightgray;
    color: v.$Dark1;
    .Theme__Option {
      background-color: v.$Grayishred;
    }

    .Theme__Option input:not(:checked) {
      background-color: v.$Grayishred;
      border-color: v.$Grayishred;
    }
    .keyboard-Calc,
    .Screen-Calc {
      background-color: v.$Grayishred;
    }

    .keyboard-Calc__Blue {
      background-color: v.$Darkmoderatecyan;
      color: v.$White;
      display: block;
      box-shadow: 0 4px 1px v.$Verydarkcyan;
      &:hover {
        background-color: color.scale(v.$Darkmoderatecyan, $lightness: -10%);
      }
    }

    .keyboard-Calc__Red {
      background-color: v.$Orange;
      color: v.$White;
      display: block;
      width: 70%;
      box-shadow: 0 4px 1px v.$Darkorange;
      &:hover {
        background-color: color.scale(v.$Orange, $lightness: -10%);
      }
    }
  }
  &.Theme3 {
    background-color: v.$Verydarkviolet;
    color: v.$Lightyellow;
    .keyboard-Calc__Grid {
      div {
        p {
          box-shadow: 0 2px 0 v.$Vividmagenta;
          background-color: v.$Verydarkviolet;
        }
      }
    }
    p {
      color: v.$Lightyellow;
    }
    .Theme__Option {
      background-color: v.$Darkviolet;
    }
    .Theme__Option input:not(:checked) {
      background-color: v.$Darkviolet;
      border-color: v.$Darkviolet;
    }

    .keyboard-Calc,
    .Screen-Calc {
      background-color: v.$Darkviolet;
    }

    .keyboard-Calc__Blue {
      background-color: v.$Darkmagenta;
      color: v.$White;
      display: block;
      box-shadow: 0 2px 0 v.$Vividmagenta;
      &:hover {
        background-color: color.scale(v.$Darkmagenta, $lightness: -10%);
      }
    }

    .keyboard-Calc__Red {
      background-color: v.$Purecyan;
      color: v.$Dark1;
      display: block;
      width: 70%;
      box-shadow: 0 2px 0 v.$Softcyan;
      &:hover {
        background-color: color.scale(v.$Purecyan, $lightness: -10%);
      }
    }
  }
}

p {
  font-size: 32px;
}
.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
