/* Tema 1 variables*/
/* Fuente principal----------------> */
$MainFont: "League Spartan", sans-serif;
/*  Backgrounds--------------------->
 */
$Dark1: hsl(222, 26%, 31%); /* main background */
$Dark2: hsl(223, 31%, 20%); /*  toggle background, keypad background */
$Dark3: hsl(224, 36%, 15%); /* screen background */

/* Keys-----------------------> */
$DarkBlue1: hsl(225, 21%, 49%); /* key background */
$DarkBlue2: hsl(224, 28%, 35%); /*  key shadow*/
$Red: hsl(6, 63%, 50%); /* key background, toggle */
$DarkRed: hsl(6, 70%, 34%); /* key shadow */
$LightGray: hsl(30, 25%, 89%); /* key background */
$GrayOrange: hsl(28, 16%, 65%); /* key shadow */

/* Text--------------------> */
$VeryDark: hsl(221, 14%, 31%);
$White: hsl(0, 0%, 100%);

/* Tema 2 variables */
/* Backgrounds */

$Lightgray: hsl(0, 0%, 90%); /* main background */
